/*
 * Component: Form
 * ---------------
 */
.form-control {
  .border-radius(@input-radius);
  box-shadow: none;
  border-color: @gray;
  &:focus {
	border-color: @light-blue;
	box-shadow: none;
  }
  &::-moz-placeholder,
  &:-ms-input-placeholder,
  &::-webkit-input-placeholder {
	color: #bbb;
	opacity: 1;
  }

  &:not(select) {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
  }
}

.form-group {
  display: flex;
  .control-label {
	display: flex;
	align-items: center;
  }

  &.has-success {
	label {
	  color: @green;
	}
	.form-control {
	  border-color: @green;
	  box-shadow: none;
	}
  }

  &.has-warning {
	label {
	  color: @yellow;
	}
	.form-control {
	  border-color: @yellow;
	  box-shadow: none;
	}
  }

  &.has-error {
	label {
	  color: @red;
	}
	.form-control {
	  border-color: @red;
	  box-shadow: none;
	}
  }
}

/* Input group */
.input-group {
  .input-group-addon {
	.border-radius(@input-radius);
	border-color: @gray;
	background-color: #fff;
  }
}
/* button groups */
.btn-group-vertical {
  .btn {
	&.btn-flat:first-of-type, &.btn-flat:last-of-type {
	  .border-radius(0);
	}
  }
}

.icheck > label {
  padding-left: 0;
}

/* support Font Awesome icons in form-control */
.form-control-feedback.fa {
	line-height: @input-height-base;
}

.input-lg + .form-control-feedback.fa,
.input-group-lg + .form-control-feedback.fa,
.form-group-lg .form-control + .form-control-feedback.fa {
  line-height: @input-height-large;
}
.input-sm + .form-control-feedback.fa,
.input-group-sm + .form-control-feedback.fa,
.form-group-sm .form-control + .form-control-feedback.fa {
  line-height: @input-height-small;
}

.required:after {
  content:" *";
  color: red;
}

input[type="password"] {
  width: 100%;
}

.form-inline {
  display: inline;
}

textarea.no-resize {
	resize: none;
}

.label-input {
	background: none;
    border: none;
    padding: 0px;
}